function email(text){
    if (text?.ErroEmail) { return '* '+text.ErroEmail; }

    text.trim();

    if (!(/^[a-z0-9](\.?[a-z0-9]){5,}@g(oogle)?mail\.com$/.test(text))
    &&text.length != 0) {
    return '* Email sai nh dng, bn kim tra li nh';
    }
    return ''
}
function password (text) {
    if (text?.ErroPassword) { return '* '+text.ErroPassword; }

    if (text.length<6&&text.length>0) {
    return '* Mt khu ti thiu 6 k t';
    }

    return ''
}
function username(text) {
    text.trim();
    if ( /[-._!"`'#%&,:;<>=@{}~\$\(\)\*\+\/\\\?\[\]\^\|]+/.test(text) ) {
        return '* Tn khng c cha k t c bit';
    }
    else if (text.length<6&&text.length>0) {
   return '* Tn ti thiu 6 k t';
    }
    else if (text.length>15) {
   return '* Tn di ti a 15 k t';  }
    
        return ''
}

export {email, password, username};
