import NavigateAccout from './NavigateAcc';
import {RenderMethodLogin,FormLogin} from './index';
import Mochimeow1 from '../assets/img/Mochimeow 1.png';
import {redirect} from '../App'
function LoginMenu({setForgotPass}){


    return(
    <>
        <h1 className='header'>
        <i className='fa-solid fa-xmark' onClick={()=>{window.location.href=redirect}}></i>ng nhp</h1>
        <h2 className='title-Mochi'>ng nhp  hc ngay nh</h2>
        <RenderMethodLogin/>
        <p className="hoacP">Hoc</p>
        <FormLogin/>
        <p onClick={()=>{setForgotPass(1)}} className="forgot">Qun mt khu?</p>

        <NavigateAccout check='login'/>
        <img className='Mochimeow1' src={Mochimeow1}/>
    </> 
)
}
export default LoginMenu;